.container.header__container {
    padding: 4rem;
    border-bottom: 1px solid rgba(0, 0, 0, 0.3);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    align-content: center;
}

.header__container>img {
    display: block;
    width: 25%;
}

.header__container>h1,
.header__container>p {
    text-align: center;
    align-content: center;
}